import './main.css';
import { Elm } from './Main.elm';
import * as serviceWorker from './serviceWorker';
import websites from './website-versions.json'


// assets
import angularJS_icon from './img/angularJS_icon.png';
import bootstrap_icon from './img/bootstrap_icon.png';
import elm_icon from './img/elm_icon.png';
import HCJ_icon from './img/HCJ_icon.png';
import jquery_icon from './img/jquery_icon.png';
import webpack_icon from './img/webpack_icon.png';

var icons = [{
    src: angularJS_icon,
    id: 'AngularJS'
}, {
    src: bootstrap_icon,
    id: 'Bootstrap'
}, {
    src: elm_icon,
    id: 'Elm'
}, {
    src: HCJ_icon,
    id: 'HTML, CSS & JS'
}, {
    src: jquery_icon,
    id: 'JQuery'
}, {
    src: webpack_icon,
    id: 'Webpack'
}]


Elm.Main.init({
    node: document.getElementById('root'),
    flags: {
        websites: websites,
        icons: icons
    }
});

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
